@import url(https://fonts.googleapis.com/css?family=Poppins:400,400i,700);
*,*:after,*:before{margin:0;padding:0;box-sizing:border-box}body{font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
.App{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.App .title{margin-bottom:5vh}
.accordion{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center;width:calc(30% + 10rem);margin:auto;list-style-type:none;transition:.5s ease-in-out}
.RBLSiteContentAccordion-accordion-item{margin-top:20px;border-top:1px solid gray;width:100%;cursor:pointer}.RBLSiteContentAccordion-accordion-item .title-group{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:10px;-webkit-align-items:baseline;align-items:baseline;text-align:left;text-align:initial;margin:20px 0}.RBLSiteContentAccordion-accordion-item .title-group .title{margin-right:5px;text-align:left;text-align:initial;color:#1917bb}.RBLSiteContentAccordion-accordion-item .title-group .title:hover{color:red !important}.RBLSiteContentAccordion-accordion-item .title-group .chevron{cursor:pointer}.RBLSiteContentAccordion-accordion-item .title-group .chevron:hover{color:red}.RBLSiteContentAccordion-accordion-item .accordion-item-text{padding:0 5px;text-align:left}.rotate{-webkit-transform:rotate(130deg);transform:rotate(130deg)}.show{visibility:visible}.hide{visibility:collapse;height:0px}
